import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import axios from 'axios';
import Img from './stop.png';

class App extends Component {

  constructor(props) {
    super(props)
    this.state = {
      err:'',
      result:''
    }
  }
  

  onFileHandler = (e) =>{
    
    if(e.target.files.length >= 2){
    this.setState({err:''})
    const data = new FormData();
    data.append('file', e.target.files[0]);
    data.append('file', e.target.files[1]);

    axios.post('/predict', data, {
      headers: {
        'Content-Type': `multipart/form-data`,
      }
    })
      .then((response) => {
        console.log(response);
        this.setState({result:response.data})
        
      }).catch((error) => {
        //handle error
      });
    }

    else{
      this.setState({err:'please select two images'})
    }

  }

  //convert base64 to file before uploading if you have base64 image

  dataURLtoFile = (dataurl, filename) => {
    const arr = dataurl.split(',')
    const mime = arr[0].match(/:(.*?);/)[1]
    const bstr = atob(arr[1])
    let n = bstr.length
    const u8arr = new Uint8Array(n)
    while (n) {
        u8arr[n - 1] = bstr.charCodeAt(n - 1)
        n -= 1 // to make eslint happy
    }
    return new File([u8arr], filename, { type: mime })
}

  render() {
    console.log(Img);
    
    return (
      <div className="App">
        <p>select two images for Face pridictions</p>
        <input type="file" multiple onChange={(e) =>this.onFileHandler(e)}/>
        
        <p style={{color:'red'}}>
          {this.state.err}
          {this.state.result.prediction === '[False]' ? 
          `Opps! images not have similar faces! try again`
          :''
         }
        </p>

        <p style={{color:'green'}}>
         {this.state.result.prediction === '[True]' ? 
          `Both images contain same face with the confidence of ${this.state.result.confidence} probability, make sure near zero values represents positive predictions`
          :''
         }

        </p>
      </div>
    );
  }
}

export default App;
